.animalGrid {
   justify-content: center;
    display: grid;
    grid-template-columns: repeat(4, minmax(20vw, 20vw));
    grid-template-rows: auto auto auto;
    grid-column-gap: 60px;
    -webkit-column-gap: 60px;
            column-gap: 60px;
    grid-row-gap: 60px;
    row-gap: 60px;
    margin-bottom: 40px;
}

.animalCards {
    border-radius: 10px; 
    background-color: rgb(240, 240, 240);
    position: relative;
}

.animalCardsProfile {
  border-radius: 10px; 
  position: relative;
}

.animalPhoto {
   width: 180px;
   border-radius: 10px;
   margin: 30px auto;

}

.animalName {
    text-align: center;
    margin-bottom: 30px;
    font-size: 1.8em;
    color:#455;
    font-weight: lighter;
}

.overlay {
    position: absolute;
    border-radius: 10px;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(240, 240, 240, 1);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.6em;
    font-weight: lighter;
    opacity: 0;
    transition: opacity 0.35s;
  }

  .overlay:hover {
    opacity: 1;
  }
  
  .overlay > * {
    transform: translateY(20px);
    transition: transform 0.35s;
  }
  
  .overlay:hover > * {
    transform: translateY(0);
  }
  .text-overlay {
    text-align: center;
  }

.meetLink {
     background-color: #455;
     border-radius: 10px;
     padding: 10px 20px;
   
}

.meetLink:hover {
      text-decoration: none;
     color: white;
}
